import Streamable from "../core/Streamable";

export default class CRC1 extends Streamable {
    constructor() {
        super(null);
        this._digest = 0;
    }
    
    reset() {
        this._digest = 0;
    }
    
    update( bytes ) {
        for ( var i = 0; i < bytes.length; ++i ) {
            this._digest = (this._digest + bytes[i]) & 0xFF;
        }
    }
    
    final() {
        return this._digest >>> 0;
    }
}